* {
  box-sizing: border-box; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  line-height: 1.5;
  color: #24292e;
  background-color: #fff;
  word-wrap: break-word; }

article {
  padding: 32px; }
  article > :first-child {
    margin-top: 0 !important; }
  article > :last-child {
    margin-bottom: 0 !important; }

blockquote {
  color: #6a737d;
  margin: 0px 0px 16px;
  padding: 0px 16px;
  border-left: 0.25em solid #e1e4e8; }
  blockquote > :last-child {
    margin-bottom: 0px; }

button {
  margin: 0;
  overflow: visible;
  position: relative;
  display: inline-block;
  padding: 6px 12px;
  font-family: inherit;
  font-size: 14px;
  line-height: inherit;
  font-weight: 600;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  background-repeat: repeat-x;
  background-position: -1px -1px;
  background-size: 110% 110%;
  border: 1px solid rgba(27, 31, 35, 0.2);
  border-radius: 0.25em;
  color: #24292e;
  background-color: #eff3f6;
  background-image: linear-gradient(-180deg, #fafbfc 0%, #eff3f6 90%); }
  button:hover {
    background-color: #e6ebf1;
    background-image: linear-gradient(-180deg, #f0f3f6 0%, #e6ebf1 90%);
    background-position: -.5em;
    border-color: rgba(27, 31, 35, 0.35);
    text-decoration: none;
    background-repeat: repeat-x; }
  button:focus {
    box-shadow: 0 0 0 0.2em rgba(3, 102, 214, 0.3);
    outline: 0; }
  button:active {
    background-color: #e9ecef;
    background-image: none;
    border-color: rgba(27, 31, 35, 0.35);
    box-shadow: inset 0 0.15em 0.3em rgba(27, 31, 35, 0.15); }

code,
tt {
  padding: 0.2em 0.4em;
  margin: 0;
  font-size: 85%;
  background-color: rgba(27, 31, 35, 0.05);
  border-radius: 3px; }
  code br,
  tt br {
    display: none; }

code {
  font-family: SFMono-Regular, Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: 85%;
  background-color: #f6f8fa;
  padding: 0.2em 0.4em;
  border-radius: 3px; }

del code {
  text-decoration: inherit; }

pre {
  word-wrap: normal; }
  pre > code {
    padding: 0;
    margin: 0;
    font-size: 100%;
    word-break: normal;
    white-space: pre;
    background: transparent;
    border: 0; }
  pre pre {
    margin-bottom: 0;
    word-break: normal; }

pre {
  margin-top: 0;
  margin-bottom: 16px;
  padding: 16px;
  overflow: auto;
  line-height: 1.45;
  background-color: #f6f8fa;
  border-radius: 3px; }

pre code,
pre tt {
  display: inline;
  padding: 0;
  margin: 0;
  overflow: visible;
  font-size: 85%;
  line-height: inherit;
  word-wrap: normal;
  background-color: transparent;
  border: 0; }

header {
  z-index: 32;
  display: flex;
  padding: 16px;
  color: rgba(255, 255, 255, 0.7);
  background-color: #24292e;
  align-items: center;
  flex-wrap: nowrap; }
  header a {
    color: #fff;
    white-space: nowrap; }
    header a:hover, header a:focus {
      color: rgba(255, 255, 255, 0.7);
      text-decoration: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 24px;
  margin-bottom: 16px;
  font-weight: 600;
  line-height: 1.25; }
  h1 a,
  h2 a,
  h3 a,
  h4 a,
  h5 a,
  h6 a {
    color: #1b1f23;
    vertical-align: middle;
    visibility: hidden; }
  h1:hover a,
  h2:hover a,
  h3:hover a,
  h4:hover a,
  h5:hover a,
  h6:hover a {
    text-decoration: none; }
  h1 tt,
  h1 code,
  h2 tt,
  h2 code,
  h3 tt,
  h3 code,
  h4 tt,
  h4 code,
  h5 tt,
  h5 code,
  h6 tt,
  h6 code {
    font-size: inherit; }

h1 {
  padding-bottom: 0.3em;
  font-size: 2em;
  border-bottom: 1px solid #eaecef; }

h2 {
  padding-bottom: 0.3em;
  font-size: 1.5em;
  border-bottom: 1px solid #eaecef; }

h3 {
  font-size: 1.25em; }

h4 {
  font-size: 1em; }

h5 {
  font-size: 0.875em; }

h6 {
  font-size: 0.85em;
  color: #6a737d; }

hr {
  height: 0;
  margin: 15px 0;
  overflow: hidden;
  background: transparent;
  border: 0;
  border-bottom: 1px solid #dfe2e5; }

img {
  max-width: 100%;
  box-sizing: content-box;
  background-color: white; }

input:not([type="checkbox"]):not([type="radio"]) {
  margin: 0;
  overflow: visible;
  font-family: inherit;
  font-size: 14px;
  line-height: inherit;
  min-height: 34px;
  padding: 6px 8px;
  color: #24292e;
  vertical-align: middle;
  background-color: #fff;
  background-repeat: no-repeat;
  background-position: right 8px center;
  border: 1px solid #d1d5da;
  border-radius: 3px;
  outline: none;
  box-shadow: inset 0 1px 2px rgba(27, 31, 35, 0.075); }
  input:not([type="checkbox"]):not([type="radio"]):focus {
    border-color: #2188ff;
    outline: none;
    box-shadow: inset 0 1px 2px rgba(27, 31, 35, 0.075), 0 0 0 0.2em rgba(3, 102, 214, 0.3); }

[type="checkbox"], [type="radio"] {
  padding: 0; }

::placeholder {
  color: #6a737d; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

label {
  font-weight: 600;
  font-size: 14px; }

a {
  color: #0366d6;
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }

ul, ol {
  padding-left: 2em; }

ul li + li {
  margin-top: 4px; }

dl {
  padding: 0px; }
  dl dt {
    margin-top: 16px;
    font-size: 1em;
    font-style: italic;
    font-weight: 600;
    padding: 0px; }
  dl dd {
    padding: 0px 16px;
    margin: 0px 0px 16px; }

select {
  min-height: 34px;
  padding: 6px 8px;
  font-family: inherit;
  font-size: 14px;
  line-height: inherit;
  color: #24292e;
  vertical-align: middle;
  background-color: #fff;
  background-repeat: no-repeat;
  background-position: right 8px center;
  border: 1px solid #d1d5da;
  border-radius: 3px;
  outline: none;
  box-shadow: inset 0 1px 2px rgba(27, 31, 35, 0.075);
  display: inline-block;
  max-width: 100%;
  height: 34px;
  padding-right: 24px;
  background: #fff url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAUCAMAAACzvE1FAAAADFBMVEUzMzMzMzMzMzMzMzMKAG/3AAAAA3RSTlMAf4C/aSLHAAAAPElEQVR42q3NMQ4AIAgEQTn//2cLdRKppSGzBYwzVXvznNWs8C58CiussPJj8h6NwgorrKRdTvuV9v16Afn0AYFOB7aYAAAAAElFTkSuQmCC") no-repeat right 8px center;
  background-size: 8px 10px;
  appearance: none; }
  select::-ms-expand {
    opacity: 0; }
  select[multiple] {
    height: auto; }

table {
  display: block;
  width: 100%;
  margin: 0px 0px 16px;
  overflow: auto;
  border-spacing: 0;
  border-collapse: collapse; }
  table th {
    font-weight: 600; }
  table th,
  table td {
    padding: 8px 16px;
    border: 1px solid #dfe2e5; }
  table tr {
    background-color: #fff;
    border-top: 1px solid #c6cbd1; }
    table tr:nth-child(2n) {
      background-color: #f6f8fa; }
  table img {
    background-color: transparent; }

p {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  line-height: 1.5;
  color: #24292e;
  margin: 0px 0px 16px; }

i {
  font-style: italic; }

b {
  font-weight: 600; }

em {
  font-weight: 600; }

small {
  font-size: 12px; }

b,
strong {
  font-weight: 600; }

textarea {
  margin: 0;
  overflow: visible;
  font-family: inherit;
  font-size: 14px;
  line-height: inherit;
  min-height: 34px;
  padding: 6px 8px;
  color: #24292e;
  vertical-align: middle;
  background-color: #fff;
  background-repeat: no-repeat;
  background-position: right 8px center;
  border: 1px solid #d1d5da;
  border-radius: 3px;
  outline: none;
  box-shadow: inset 0 1px 2px rgba(27, 31, 35, 0.075); }
  textarea:focus {
    border-color: #2188ff;
    outline: none;
    box-shadow: inset 0 1px 2px rgba(27, 31, 35, 0.075), 0 0 0 0.2em rgba(3, 102, 214, 0.3); }
